import './perfil_usuario.css'
import React, {useEffect,useState} from 'react'
import foto from'./img.jpg' 
import { useParams } from 'react-router-dom'
const Perfil = () => {

    const {id}= useParams()

    const [perfil, setPerfil] = useState([])  
 
    const initialUrl=`http://localhost:8080/api/deportista/buscar/${id}`;
    
    
      const fetchPerfil=(url)=>{
        fetch(url)
          .then(response =>response.json())
          .then(data =>setPerfil(data))
          .catch(error => console.log(error))
      }
      useEffect(() => {
        fetchPerfil(initialUrl);
      }, [])
    

  return (
    <section class="seccion-perfil-usuario">
        <div class="perfil-usuario-header">
            <div class="perfil-usuario-portada">
                <div class="perfil-usuario-avatar">
                    <img src={foto} alt="" />
                    <button type="button" class="boton-avatar">
                        <i class="far fa-image"></i>
                    </button>
                </div>
                
            </div>
        </div>
        <div class="perfil-usuario-body">
            <div class="perfil-usuario-bio">
                <h3 class="titulo">{perfil.nombre} {perfil.apellidos}</h3>
                <p class="texto">{perfil.descripcion}</p>
            </div>
            <div class="perfil-usuario-footer">
                <ul class="lista-datos">
                    <li><i class="icono fas fa-phone-alt"></i> {perfil.correo}</li>
                    <li><i class="icono fas fa-briefcase"></i> {perfil.estatura} cm</li>
                    <li><i class="icono fas fa-building"></i>{perfil.peso} kg</li>
                </ul>
                <ul class="lista-datos">
                    <li><i class="icono fas fa-map-marker-alt"></i> {perfil.comuna}</li>
                    <li><i class="icono fas fa-calendar-alt"></i> {perfil.edad}</li>
                    <li><i class="icono fas fa-user-check"></i> {perfil.redSocialPrincipal}</li>
                    
                </ul>
            </div>
            
        </div>
    </section>


    
  )
}

export default Perfil