import React, {useEffect,useState} from 'react'
/* import { Link } from "react-router-dom"; */

import './listar_equipos.css'
import Busqueda from '../../components/Busqueda/Busqueda';

const ListarEquipos = () => {

  const [equipos, setEquipos] = useState([])  
 
const initialUrl="http://localhost:8080/api/equipo/todos";


  const fetchEquipos=(url)=>{
    fetch(url)
      .then(response =>response.json())
      .then(data =>setEquipos(data))
      .catch(error => console.log(error))
  }
  useEffect(() => {
    fetchEquipos(initialUrl);
  }, [])
 
  return (
    <div className='mt-3'>
      <div className='mb-5'> 
        <Busqueda />
      </div>
    <br />
    {equipos.map((item, index)=>(
      <div key={index}>
       <div className='card mt-5'>
       <div class="card-header">
           {item.id}
       </div>
     
         <div class="card-body">
             <h5 className="card-title">{item.nombre} </h5>
             <p className="card-text">{item.descripcion}</p>
           {/*   <div  className="btn btn-warning">Unirme</div>
             <Link className="btn btn-warning" to="/Equipo">Ver Detalle</Link> */}
         </div>
     </div></div>
    ))}
     
    </div>

)
}

export default ListarEquipos